import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  IRecruitmentRequestDisplayModel,
  IRecruitmentRequestModel,
} from "../models/recruitment-request";

export function recruitmentRequestToDisplay(
  data: IRecruitmentRequestModel[]
): IRecruitmentRequestDisplayModel[] {
  return data.map((request) => {
    return <IRecruitmentRequestDisplayModel>{
      id: request.request?.id,
      jobTitle: msgTranslate(
        request.job_Title?.ar_Name,
        request.job_Title?.en_Name
      ),
      vacantPlaces: request.request?.emp_No,
      fromDateExpected: HelpersFunctions.mapDate(request.request?.from_Date),
      toDateExpected: HelpersFunctions.mapDate(request.request?.to_Date),
      notes: request.request?.note,
    };
  });
}
