import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { recruitmentRequestToDisplay } from "app/recruitment/mapper/recruitment-request";
import {
  IRecruitmentRequestDisplayModel,
  IRecruitmentRequestModel,
} from "app/recruitment/models/recruitment-request";
import { RecruitmentRequestService } from "app/recruitment/services/recruitment-request.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-recruitment-request-index",
  templateUrl: "./recruitment-request-index.component.html",
  styleUrls: ["./recruitment-request-index.component.scss"],
})
export class RecruitmentRequestIndexComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<IRecruitmentRequestDisplayModel>;
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "jobTitle",
    "vacantPlaces",
    "fromDateExpected",
    "toDateExpected",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];

  constructor(public recruitmentRequestService: RecruitmentRequestService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  ngOnInit(): void {}

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    this.removePageParams();
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.recruitmentRequestService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IRecruitmentRequestModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(
            recruitmentRequestToDisplay(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        } else this.errorList(res?.message?.messages);
      });
  }

  onDelete(request: IRecruitmentRequestDisplayModel) {
    let msg = `${request.jobTitle} - ${request.fromDateExpected} - ${request.toDateExpected}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(request);
    });
  }

  delete(request: IRecruitmentRequestDisplayModel) {
    this.showLoading();
    this.recruitmentRequestService
      .Delete(request.id)
      .subscribe((res: IResponse) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            this.getList(this.prevPage, this.page.pageSize);
          } else {
            this.getList(this.currentPage, this.page.pageSize);
          }
          this.deleteSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }
}
