import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { IJobTitleModel } from "app/org-management/model/job-title";
import {
  IRecruitmentRequestModel,
  IRecruitmentRequestRequest,
} from "app/recruitment/models/recruitment-request";
import { RecruitmentRequestService } from "app/recruitment/services/recruitment-request.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { IOrganizationTypeModel } from "app/system-setup/org-management-setup/models/organization-types";
import { OrganizationTypesService } from "app/system-setup/org-management-setup/services/organization-types.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-add-recruitment-request",
  templateUrl: "./add-recruitment-request.component.html",
  styleUrls: ["./add-recruitment-request.component.scss"],
})
export class AddRecruitmentRequestComponent
  extends AppHelpers
  implements OnInit
{
  requestId: number = 0;

  form: FormGroup;
  submitted: boolean = false;

  showJobTitleDetails: boolean = false;

  constructor(
    private recruitmentRequestService: RecruitmentRequestService,
    private organizationTypesService: OrganizationTypesService
  ) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.requestId > 0 && this.checkNoBusinessOrCompany()) {
      this.getRequestById();
    }
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [this.requestId, { nonNullable: true }],
        org_type_ID: [null, [Validators.required]],
        org_ID: [null, [Validators.required]],
        job_Title_ID: [null, [Validators.required]],
        employementType_ID: [null, [Validators.required]],
        request_Reason_ID: [null, [Validators.required]],
        gender_ID: [null, [Validators.required]],
        emp_No: [null, [CustomValidators.gte(1)]],
        from_Date: [null, [Validators.required]],
        to_Date: [null, [Validators.required]],
        other_requirment: [null],
        note: [null],
      },
      {
        validator: [CustomValidator.compareDates("from_Date", "to_Date")],
      }
    );

    this.activatedRoute.params.subscribe((param) => {
      this.requestId = Number(param.id ?? 0);
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  getRequestById() {
    this.showLoading("form");
    this.recruitmentRequestService
      .getById(this.requestId)
      .subscribe((res: IResponse<IRecruitmentRequestModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.form.patchValue(res.data[0].request);
          this.formCtrls.gender_ID.setValue(res.data[0].gender_ID);
          this.getOrgTypeByOrgId(res.data[0].org.id, res.data[0].job_Title.id);
          // this.showJobTitleDetails = true;
          // hideLoading >> in getOrgTypeByOrgId function
        } else {
          this.errorList(res?.message?.messages);
          this.hideLoading("form");
        }
      });
  }

  getOrgTypeByOrgId(orgId: number, jobTitleId: number) {
    this.organizationTypesService
      .getByOrgId(orgId)
      .subscribe((res: IResponse<IOrganizationTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.formCtrls.org_type_ID.setValue(res.data[0].id);
          this.formCtrls.org_ID.setValue(orgId);
          setTimeout(() => {
            this.formCtrls.job_Title_ID.setValue(jobTitleId);
          }, 50);
          this.hideLoading("form");
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      let model: IRecruitmentRequestRequest = {
        rec_Request: {
          id: this.form.value.id,
          emp_No: this.form.value.emp_No,
          from_Date: HelpersFunctions.mapDateReverse(this.form.value.from_Date),
          to_Date: HelpersFunctions.mapDateReverse(this.form.value.to_Date),
          request_Reason_ID: this.form.value.request_Reason_ID,
          employementType_ID: this.form.value.employementType_ID,
          other_requirment: this.form.value.other_requirment,
          note: this.form.value.note,
        },
        org_ID: this.form.value.org_ID,
        job_Title_ID: this.form.value.job_Title_ID,
        gender_ID: this.form.value.gender_ID,
      };

      model.rec_Request.id == 0 ? this.create(model) : this.update(model);
    }
  }

  create(data: IRecruitmentRequestRequest) {
    this.showLoading("form");
    this.recruitmentRequestService
      .create(data)
      .subscribe((res: IResponse<IRecruitmentRequestRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res.message.messageTypeName);
          this.requestId = res.data.rec_Request.id;
          this.formCtrls.id.setValue(this.requestId);
          let url = `/recruitment/${this.moduleId}/request/edit/${this.requestId}`;
          this.navigateTo(url);
        } else this.errorList(res?.message?.messages);
        this.hideLoading("form");
      });
  }

  update(data: IRecruitmentRequestRequest) {
    this.showLoading("form");
    this.recruitmentRequestService
      .update(data)
      .subscribe((res: IResponse<IRecruitmentRequestRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading("form");
      });
  }

  clearForm() {
    this.form.reset();
    this.submitted = false;
  }

  onChangeJobTitle(values: IJobTitleModel[]) {
    this.showJobTitleDetails = values.length > 0 ? true : false;
  }
}
