import { Component, OnInit, ViewChild } from "@angular/core";
import { MatStepper } from "@angular/material/stepper";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-add-candidate-core",
  templateUrl: "./add-candidate-core.component.html",
  styleUrls: ["./add-candidate-core.component.scss"],
})
export class AddCandidateCoreComponent extends AppHelpers implements OnInit {
  candidateId: number = 0;
  @ViewChild("stepper") stepper: MatStepper;
  jobTitle: string;

  constructor() {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      this.candidateId = Number(param.id ?? 0);
    });
  }

  stepChanged() {
    // prevent inputs to be invalid after next & back
    this.stepper.selected.interacted = false;
  }

  getCandidateId(value) {
    this.candidateId = value;
  }
}
