import { Injectable } from "@angular/core";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";
import { recruitmentRequestToDisplay } from "../mapper/recruitment-request";
import {
  IRecruitmentRequestModel,
  IRecruitmentRequestRequest,
} from "../models/recruitment-request";

@Injectable({
  providedIn: "root",
})
export class RecruitmentRequestService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IRecruitmentRequestRequest) {
    return this.post<IResponse<IRecruitmentRequestRequest>>(
      "Recruitment/Request/Create",
      data
    );
  }

  update(data: IRecruitmentRequestRequest) {
    return this.post<IResponse<IRecruitmentRequestRequest>>(
      "Recruitment/Request/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IRecruitmentRequestModel[]>>(
      "Recruitment/Request/GetList",
      {
        params: {
          ...params,
          // $expand: `${db.rec_request},${db.Core_Job_Title}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IRecruitmentRequestModel[]>>(
      "Recruitment/Request/GetAll"
      // {
      //   params: {
      //     $expand: `${db.rec_request},${db.Core_Job_Title}`,
      //   },
      // }
    );
  }

  getById(id: number) {
    return this.get<IResponse<IRecruitmentRequestModel[]>>(
      "Recruitment/Request/GetAll",
      {
        params: {
          $filter: `request_ID eq ${id}`,
          // $expand: `${db.rec_request},${db.Core_Job_Title},${db.CORE_GENDER},${db.core_Org}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Recruitment/Request/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    return this.getAll().pipe(
      map((res: IResponse<any[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          res.data = recruitmentRequestToDisplay(res.data);
          return res;
        }
      })
    );
  }
}
