import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  ICandidateModel,
  ICandidateRequest,
} from "app/recruitment/models/candidate/candidate";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "candidate-general-info",
  templateUrl: "./candidate-general-info.component.html",
  styleUrls: ["./candidate-general-info.component.scss"],
})
export class CandidateGeneralInfoComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Output() changeCandidateId = new EventEmitter<number>();

  form: FormGroup;
  submitted: boolean = false;

  constructor(private candidateService: CandidateService) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getCandidateById();
    }
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      code: [null, [Validators.required, CustomValidator.whiteSpace]],
      nationality: [null],
      nationalid: [
        null,
        [
          Validators.required,
          CustomValidator.whiteSpace,
          CustomValidators.number,
          CustomValidator.Length(Constants.nationalId),
        ],
      ],
      dob: [null],
      registrationDate: [null, [Validators.required]],
      gender_ID: [null],
      religioN_ID: [null],
      sourcingTypeId: [null],
      sourcingName: [null],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  getCandidateById() {
    this.showLoading("form");
    this.candidateService
      .getById(this.candidateId)
      .subscribe((res: IResponse<ICandidateModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.form.patchValue(res.data[0]);
        } else this.errorList(res?.message?.messages);
        this.hideLoading("form");
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateRequest = {
        id: this.form.value.id,
        ar_Name: this.form.value.ar_Name,
        en_Name: this.form.value.en_Name,
        code: this.form.value.code,
        nationality: this.form.value.nationality,
        nationalid: this.form.value.nationalid,
        dob: HelpersFunctions.mapDateReverse(this.form.value.dob),
        registrationDate: HelpersFunctions.mapDateReverse(
          this.form.value.registrationDate
        ),
        gender_ID: this.form.value.gender_ID,
        religioN_ID: this.form.value.religioN_ID,
        sourcingTypeId: this.form.value.sourcingTypeId,
        sourcingName: this.form.value.sourcingName,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateRequest) {
    this.showLoading();
    this.candidateService
      .create(model)
      .subscribe((res: IResponse<ICandidateModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.candidateId = res.data.id;
          this.changeCandidateId.emit(this.candidateId);
          this.formCtrls.id.setValue(this.candidateId);
          let url = `/recruitment/${this.moduleId}/candidate/edit/${this.candidateId}`;
          this.navigateTo(url);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: ICandidateRequest) {
    this.showLoading();
    this.candidateService
      .update(model)
      .subscribe((res: IResponse<ICandidateModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }
}
