import {
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
} from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { jobKpiToMap, jobToMap } from "app/org-management/mapper/job-title";
import {
  IJobKpiModel,
  IJobTitleModel,
} from "app/org-management/model/job-title";
import { JobTitleService } from "app/org-management/services/job-profile/job-title.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-add-recruitment-job-details",
  templateUrl: "./add-recruitment-job-details.component.html",
  styleUrls: ["./add-recruitment-job-details.component.scss"],
})
export class AddRecruitmentJobDetailsComponent
  extends AppHelpers
  implements OnInit, OnChanges
{
  @Input() jobTitleId: number = 0;
  isLoaded: boolean = false;

  jobTitle: IJobTitleModel;

  // kpi
  kpiMandatoryColomns: string[] = ["kpi"];
  kpiOptionalColomns: string[] = [];
  kpiDataSource: MatTableDataSource<IJobKpiModel>;

  constructor(private jobTitleService: JobTitleService) {
    super();
  }

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.jobTitleId?.currentValue > 0) {
      this.getDetailsById();
    }
  }

  override onChangeBusinessAndCompanyId() {
    if (this.jobTitleId > 0 && this.checkNoBusinessOrCompany()) {
      this.getDetailsById();
    }
  }

  getDetailsById() {
    this.jobTitle = null;
    this.kpiDataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.jobTitleService
      .getDetailsById(this.jobTitleId)
      .subscribe((res: IResponse<IJobTitleModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.jobTitle = jobToMap(res.data)[0];
          this.kpiDataSource = new MatTableDataSource(
            jobKpiToMap(res.data[0].core_Job_Title_KPI)
          );
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }
}
