import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { RecruitmentRequestIndexComponent } from "./components/request/recruitment-request-index/recruitment-request-index.component";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";
import { AddRecruitmentRequestComponent } from "./components/request/add-request/add-recruitment-request/add-recruitment-request.component";
import { CandidateIndexComponent } from "./components/candidate/candidate-index/candidate-index.component";
import { AddCandidateCoreComponent } from "./components/candidate/add-candidate/add-candidate-core/add-candidate-core.component";

const routes: Routes = [
  {
    path: "request",
    data: { view: ViewsManager.request },
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: RecruitmentRequestIndexComponent,
        data: { view: ViewsManager.request_index },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddRecruitmentRequestComponent,
        data: { view: ViewsManager.add_request },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: AddRecruitmentRequestComponent,
        data: { view: ViewsManager.add_request },
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "candidate",
    data: { view: ViewsManager.candidate },
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: CandidateIndexComponent,
        data: { view: ViewsManager.candidate_index },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddCandidateCoreComponent,
        data: { view: ViewsManager.add_candidate },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: AddCandidateCoreComponent,
        data: { view: ViewsManager.add_candidate },
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RecruitmentRoutingModule {}
